.home-section-slider {
	.slide-item {
		position: relative;
		.slider-text {
			position: absolute;
			top: 0;
			bottom: 0;
			left: 0;
			right: 0;
			.v-outer,
			.v-inner {
				height: 100%;
			}
			@include respond-to(s-desktops) {
				padding: 0 340px 0 90px;
			}
			@include respond-to(desktops) {
				padding: 0;
			}
		}
	}
	.owl-nav>div {
		overflow:hidden;
		white-space: nowrap;
		text-indent:101%;
		position: absolute;
		top: 50%;
		z-index: 50;
		margin-top: -20px;
		font-size: 0;
		line-height: 0;
		border: 0;
		background: none;
		cursor: pointer;
		@include respond-to(s-desktops) {
			margin-top: -35px;
		}
		@include respond-to(desktops) {
			margin-top: -48px;
		}
		&.owl-prev {
			left: 5px;
			@include respond-to(phones) {
				@include sprite($slick-prev-mob);
			}
			@include respond-to(tablets) {
				@include sprite($slick-prev-mob);
			}
			@include respond-to(s-desktops) {
				left: 10px;
				@include sprite($slick-prev-md);
			}
			@include respond-to(desktops) {
				left: 45px;
				@include sprite($slick-prev);
			}
		}
		&.owl-next {			
			right: 5px;
			@include respond-to(phones) {
				@include sprite($slick-next-mob);
			}
			@include respond-to(tablets) {
				@include sprite($slick-next);
				@include sprite($slick-next-mob);
			}
			@include respond-to(s-desktops) {
				right: 10px;
				@include sprite($slick-next-md);
			}
			@include respond-to(desktops) {
				right: 45px;
				@include sprite($slick-next);
			}	
		}
	}
	blockquote {
		padding: 0 60px;
		@include respond-to(s-desktops) {
			padding: 0;
		}
		@include respond-to(desktops) {
			padding: 0 0 0 220px;
		}
		margin: 0;
		color: #fff;
		max-width: 900px;
		q, cite {
			display: block;
		}
		q {
			font-family: $f-alt-family;
			font-style: normal;			
			text-shadow: 0 4px 5px rgba(0, 0, 0, .5);
			@include font-size(25px);
			@include respond-to(s-desktops) {
				@include font-size(24px, 40px);
			}
			@include respond-to(desktops) {
				@include font-size(35px, 45px);
			}
		}
		cite {
			@include font-size(16px);
			font-style: normal;
			padding: 15px 0 0;
			@include respond-to(s-desktops) {
				@include font-size(20px);
			}
		}
	}
}
.owl-carousel {
  display: none;
  width: 100%;
  -webkit-tap-highlight-color: transparent;
  /* position relative and z-index fix webkit rendering fonts issue */
  position: relative;
  z-index: 1; }
  .owl-carousel .owl-stage {
    position: relative;
    -ms-touch-action: pan-Y; }
  .owl-carousel .owl-stage:after {
    content: ".";
    display: block;
    clear: both;
    visibility: hidden;
    line-height: 0;
    height: 0; }
  .owl-carousel .owl-stage-outer {
    position: relative;
    overflow: hidden;
    /* fix for flashing background */
    -webkit-transform: translate3d(0px, 0px, 0px); }
  .owl-carousel .owl-item {
    position: relative;
    min-height: 1px;
    float: left;
    -webkit-backface-visibility: hidden;
    -webkit-tap-highlight-color: transparent;
    -webkit-touch-callout: none; }
  .owl-carousel .owl-item img {
    display: block;
    width: 100%;
    -webkit-transform-style: preserve-3d; }
    .owl-carousel.owl-home-carousel .owl-item img {
			width: auto;
			display: inline;
    }
  .owl-carousel .owl-nav.disabled,
  .owl-carousel .owl-dots.disabled {
    display: none; }
  .owl-carousel .owl-nav .owl-prev,
  .owl-carousel .owl-nav .owl-next,
  .owl-carousel .owl-dot {
    cursor: pointer;
    cursor: hand;
    -webkit-user-select: none;
    -khtml-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none; }
  .owl-carousel.owl-loaded {
    display: block; }
  .owl-carousel.owl-loading {
    opacity: 0;
    display: block; }
  .owl-carousel.owl-hidden {
    opacity: 0; }
  .owl-carousel.owl-refresh .owl-item {
    display: none; }
  .owl-carousel.owl-drag .owl-item {
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none; }
  .owl-carousel.owl-grab {
    cursor: move;
    cursor: grab; }
  .owl-carousel.owl-rtl {
    direction: rtl; }
  .owl-carousel.owl-rtl .owl-item {
    float: right; }

/* No Js */
.no-js .owl-carousel {
  display: block; }

/*
 *  Owl Carousel - Animate Plugin
 */
.owl-carousel .animated {
  animation-duration: 1000ms;
  animation-fill-mode: both; }

.owl-carousel .owl-animated-in {
  z-index: 0; }

.owl-carousel .owl-animated-out {
  z-index: 1; }

.owl-carousel .fadeOut {
  animation-name: fadeOut; }

@keyframes fadeOut {
  0% {
    opacity: 1; }
  100% {
    opacity: 0; } }

/*
 * 	Owl Carousel - Auto Height Plugin
 */
.owl-height {
  transition: height 500ms ease-in-out; }

/*
 * 	Owl Carousel - Lazy Load Plugin
 */
.owl-carousel .owl-item .owl-lazy {
  opacity: 0;
  transition: opacity 400ms ease; }

.owl-carousel .owl-item img.owl-lazy {
  transform-style: preserve-3d; }

/*
 * 	Owl Carousel - Video Plugin
 */
.owl-carousel .owl-video-wrapper {
  position: relative;
  height: 100%;
  background: #000; }

.owl-carousel .owl-video-play-icon {
  position: absolute;
  height: 80px;
  width: 80px;
  left: 50%;
  top: 50%;
  margin-left: -40px;
  margin-top: -40px;
  background: url("owl.video.play.png") no-repeat;
  cursor: pointer;
  z-index: 1;
  -webkit-backface-visibility: hidden;
  transition: transform 100ms ease; }

.owl-carousel .owl-video-play-icon:hover {
  -ms-transform: scale(1.3, 1.3);
      transform: scale(1.3, 1.3); }

.owl-carousel .owl-video-playing .owl-video-tn,
.owl-carousel .owl-video-playing .owl-video-play-icon {
  display: none; }

.owl-carousel .owl-video-tn {
  opacity: 0;
  height: 100%;
  background-position: center center;
  background-repeat: no-repeat;
  background-size: contain;
  transition: opacity 400ms ease; }

.owl-carousel .owl-video-frame {
  position: relative;
  z-index: 1;
  height: 100%;
  width: 100%;
}
.owl-home-carousel {
	.item {
		padding: 0 29px;
		text-align: center;
		@include respond-to(s-phones) {
			padding: 0;
		}
	}
  .v-outer,
  .v-inner {
    height: 100px;
  }
	padding: 0 35px;
	.owl-nav {
		&>* {
			cursor: pointer;
			overflow: hidden;
			white-space: nowrap;
			text-indent: 101%;
			text-align: left;
			font-size: 0;
			line-height: 0;
			border: 0;
			outline: none;
			position: absolute;
			background: none;
			top: 50%;
			margin-top: -25px;
			@include respond-to(phones) {
				margin-top: -16.5px;
			}
			&.owl-prev {
				left: 0;
				@include sprite($carousel-prev);
				left: 0;
				@include respond-to(phones) {
					@include sprite($carousel-prev-xs);
				}
			}
			&.owl-next {
				@include sprite($carousel-next);
				right: 0;
				@include respond-to(phones) {
					@include sprite($carousel-next-xs);
				}
			}
		}
	}
}